
#app{
	min-width: 600px;
	width: 100%;
}
a{
  text-decoration: none;
  color: #33495e;
}
h1, h2 {
  font-weight: normal;
  letter-spacing: 1px;
}
.container{
  width: 100%;
  background-color: pink;
  padding:25px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background-color: #fdfdfd;
  -webkit-box-pack:center;
      -ms-flex-pack:center;
          justify-content:center;
}
.logo img{
  max-width: 10%;
  min-width: 50px;
}
.container .myname{
  display: block;
  min-width: 144px;
  padding:1% 0;
  text-shadow:1px 1px 1px pink;
}
.container .navbar{
  /*float: left;*/
  width: 30%;
  padding: 1% 10%;
  min-width: 243px;
}
.navbar li{
  float: left;
  margin-right: 5%;
}
.container .github{
  padding: 1% 0;
}



