
.box{
	 margin: -280px auto;
}
.unit{
 	width: 100%;
 	height: 100%;
 	position: relative;
	margin:50px auto;
 	/*当一个元素内部有3d,自己也要进行选择，一定要加上transform-style:preserve-3d,元素内部的3d变化将会保留*/
 	-webkit-transform-style:preserve-3d;
 	        transform-style:preserve-3d;
}
div.grid{
 	width: 60px;
 	height: 70px;
 	background-color: pink;
 	opacity: 0.4;
 	position: absolute;
 	top:50%;
 	left:50%;
 	margin-left:-30px;
 	margin-top:-20px;
}



